import { createTheme, ThemeProvider } from '@mui/material/styles';

import { Typography, TextField, Button, Grid } from '@mui/material';
import MainCard from 'ui-component/cards/MainCard';
import { useState, useEffect } from 'react';
import { Box } from '@mui/system';

const darkTheme = createTheme({
  palette: {
    mode: 'dark'
  }
});

const Page = () => {
  const [message, setMessage] = useState('');
  const [error, setError] = useState(false);
  const [outputData, setOutputData] = useState(null);
  const [outputError, setOutputError] = useState(null);
  const [hovered, setHovered] = useState(false);

  useEffect(() => {
    document.title = `LsCo Software - Validate Tokens`;
  }, []);

  const handleClick = async () => {
    if (message.trim() === '') {
      setError(true);
      setOutputData(null);
      setOutputError('This field is required');
    } else {
      try {
        const data = await validateToken(message);

        setError(false);
        setOutputData(data);
        setOutputError(null);
      } catch (err) {
        setError(true);
        setOutputData(null);
        setOutputError(err.message);
      }
    }
  };

  const validateToken = async (token) => {
    const validateResponse = await fetch('https://id.twitch.tv/oauth2/validate', {
      headers: {
        Authorization: `OAuth ${token}`
      }
    });

    const validateData = await validateResponse.json();

    if (!validateResponse.ok) {
      switch (validateData.message) {
        case 'invalid access token':
          throw new Error('Invalid access token');
        default:
          throw new Error(validateData.message || 'Validate token failed');
      }
    }

    return validateData;
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      handleClick();
    }
  };

  const handleMouseEnter = () => {
    setHovered(true);
  };

  const handleMouseLeave = () => {
    setHovered(false);
  };

  return (
    <MainCard title="Validate Tokens" description="If you will check an access token, you can do it.">
      <Grid container spacing={2} alignItems="center">
        <Grid item xs={12}>
          <ThemeProvider theme={darkTheme}>
            <TextField
              fullWidth
              id="outlined-basic"
              label="OAuth Token"
              type="password"
              variant="outlined"
              value={message}
              onChange={(e) => setMessage(e.target.value)}
              error={error}
              helperText={outputError}
              onKeyPress={handleKeyPress}
            />
          </ThemeProvider>
        </Grid>
        <Grid item xs={12}>
          <Button variant="contained" fullWidth onClick={handleClick}>
            Validate
          </Button>
        </Grid>
        <Grid item xs={12}>
          {outputData && (
            <Box
              fullWidth
              sx={{
                backgroundColor: 'rgb(250,250,250,0.1)',
                padding: '2.5px',
                paddingX: '15px',
                whiteSpace: 'pre-wrap',
                maxWidth: '100%',
                overflowX: 'auto'
              }}
            >
              <div style={{ padding: '5px', marginTop: '10px' }}>
                <Typography variant="body2" component="pre">
                  <code>
                    Login: <span style={{ color: '#00B8D9' }}>{outputData.login}</span> - (ID:{' '}
                    <span style={{ color: '#00B8D9' }}>{outputData.user_id}</span>)
                    <br />
                    Token:{' '}
                    <span
                      className={hovered ? '' : 'blur-on-hover'}
                      style={{ color: '#00B8D9' }}
                      onMouseEnter={handleMouseEnter}
                      onMouseLeave={handleMouseLeave}
                    >
                      {message}
                    </span>
                    <br />
                    ClientID: <span style={{ color: '#00B8D9' }}>{outputData.client_id}</span>
                    <br />
                    Scopes: <span style={{ color: '#00B8D9' }}>{outputData.scopes ? outputData.scopes.join(', ') : 'No scopes'}</span>
                  </code>
                </Typography>
              </div>
            </Box>
          )}
        </Grid>
      </Grid>
    </MainCard>
  );
};

export default Page;
